import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init: function (editor) {
    const pluginConfig = CKEDITOR.tools.get_plugin_config(pluginName, editor);
    const colors = pluginConfig;

    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init: function () {
        this.add('default', 'Remove Color', 'Remove Color');
        console.log('colors', colors);
        for (let label in colors) {
          const colorValue = colors[label];
          this.add(colorValue, label, label);
        }
      },
      onRender: function () {
        this.setValue('');
      },
      onClick: function (value) {
        editor.focus();

        if (value === 'default') {
          const selection = editor.getSelection();
          if (!selection) {
            console.warn('âš ï¸ No selection found.');
            return;
          }

          const bookmarks = selection.createBookmarks();
          const ranges = selection.getRanges();

          editor.fire('lockSnapshot');
          console.log('ðŸŸ¡ Starting smart color removalâ€¦');

          ranges.forEach((range, index) => {
            console.log(`ðŸ“Œ Processing range ${index + 1}...`);
            range.enlarge(CKEDITOR.ENLARGE_INLINE);

            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return (
                node.type === CKEDITOR.NODE_ELEMENT &&
                node.getName() === 'span' &&
                node.getStyle('color')
              );
            };

            let node;
            let processedNodes = new Set();

            while ((node = walker.next())) {
              if (processedNodes.has(node)) continue;
              processedNodes.add(node);

              console.log('ðŸŽ¯ Found span with color:', node.getOuterHtml());
              node.removeStyle('color');
              safeUnwrap(node);
            }

            // Smart partial removal
            smartRemoveColorFromPartial(range);
          });

          selection.selectBookmarks(bookmarks);
          editor.fire('unlockSnapshot');
          console.log('ðŸŽ‰ Finished smart color removal.');
        } 
        else {
          const style = new CKEDITOR.style({
            element: 'span',
            styles: { color: value }
          });

          editor.fire('lockSnapshot');
          editor.applyStyle(style);
          editor.fire('unlockSnapshot');
        }
      }
    });

    function safeUnwrap(element) {
      if (!element) return;

      const hasAnyAttributes = element.hasAttributes();
      const styleAttr = element.getAttribute('style');
      const hasStyles = styleAttr && styleAttr.trim() !== '';

      if (!hasAnyAttributes || (!hasStyles && element.getName() === 'span')) {
        const children = element.getChildren().toArray();
        for (let child of children) {
          element.insertBeforeMe(child.remove());
        }
        element.remove();
        console.log('ðŸ§¹ Safely unwrapped empty span.');
      }
    }

    function smartRemoveColorFromPartial(range) {
      ['startContainer', 'endContainer'].forEach((containerKey) => {
        const container = range[containerKey];
        if (!container) return;

        const span = container.getAscendant(function (el) {
          return el.getName && el.getName() === 'span' && el.getStyle('color');
        }, true);

        if (span) {
          console.log('âš ï¸ Partial boundary span detected for smart split:', span.getOuterHtml());

          const parentStyles = span.getAttribute('style') || '';
          const keptStyle = parentStyles
            .split(';')
            .map(s => s.trim())
            .filter(s => s && !s.startsWith('color'))
            .join('; ');

          const bookmark = range.createBookmark();

          // Create a temporary range fully inside that span
          const tmpRange = range.clone();
          tmpRange.setStartBefore(span);
          tmpRange.setEndAfter(span);

          const frag = tmpRange.extractContents();
          const walker = new CKEDITOR.dom.walker(new CKEDITOR.dom.range(frag));
          walker.evaluator = node => node.type === CKEDITOR.NODE_TEXT;

          let textNode;
          while ((textNode = walker.next())) {
            const insideRange = range.intersectsNode(textNode);
            if (insideRange) {
              const parent = textNode.getParent();
              if (parent.getName() !== 'span') {
                const newSpan = new CKEDITOR.dom.element('span');
                if (keptStyle) newSpan.setAttribute('style', keptStyle);
                textNode.insertBeforeMe(newSpan);
                newSpan.append(textNode.remove());
              } else {
                parent.setAttribute('style', keptStyle);
              }
            }
          }

          tmpRange.insertNode(frag);
          safeUnwrap(span);
          range.moveToBookmark(bookmark);
        }
      });
    }
  }
});
